import React from 'react';
import { useState, useEffect } from 'react';
import { v4 } from 'uuid';

import {socket} from '../../services/taixiu';

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Close from '../../assets/img/icons/close.svg';

import dice1 from '../../assets/img/dices/1.svg';
import dice2 from '../../assets/img/dices/2.png';
import dice3 from '../../assets/img/dices/3.png';
import dice4 from '../../assets/img/dices/4.png';
import dice5 from '../../assets/img/dices/5.png';
import dice6 from '../../assets/img/dices/6.png';



import Dot1 from '../../assets/img/dices/dot-1.png';
import Dot4 from '../../assets/img/dices/dot-4.png';
import Dot6 from '../../assets/img/dices/dot-6.png';
import Dot1Red from '../../assets/img/dices/dot-1-red.png';
import Dot4Red from '../../assets/img/dices/dot-4-red.png';
import Dot6Red from '../../assets/img/dices/dot-6-red.png';

// {dice(props.result.result1)}{dice(props.result.result2)}{dice(props.result.result3)}

function ResultHistory() {

    function dice (v) {
        if (v === 1) return <img src={dice1} alt="Dot1"/>
        if (v === 2) return <img src={dice2} alt="Dot1"/>
        if (v === 3) return <img src={dice3} alt="Dot1"/>
        if (v === 4) return <img src={dice4} alt="Dot1"/>
        if (v === 5) return <img src={dice5} alt="Dot1"/>
        if (v === 6) return <img src={dice6} alt="Dot1"/>
    }

    function side (value) {
        if (value === "xiu") return <td><span className="color-red">XIU</span></td>
        else return <td><span className="color-green">TAI</span></td>
    }

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [history, setHistory] = useState([]);

    useEffect(() => {
        socket.on('RESULT', (...args) => {
            let temp = history;
            temp.unshift(args[0]);
            setHistory(temp);
        });
    
        return () => {
          socket.off('RESULT');
        };
    }, []);
    // {game_id: '12425', won_side: 'xiu', result1: 6, result2: 1, result3: 3}
    return ( 
        <>
            <span onClick={handleShow} className="history_tab borderBtn">Result History</span>

            <Modal className='historyModal' show={show} onHide={handleClose}>
                <div className='modal_header'>
                    <h2 className='modal_title'>Result History</h2>
                    <img src={Close} alt="Close" onClick={handleClose} className="closeModal" />
                </div>
                <Modal.Body>
                    <div className="historyTable">
                        <table className="table">
                            <thead>
                                <tr>
                                    <th>Game ID</th>
                                    <th>3 Dices</th>
                                    <th>Winning side</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td># 23265323</td>
                                    <td>
                                        <div className="threeDices">
                                            <div className="singleDices">
                                                <img src={Dot4} alt="Dot4" />
                                            </div>
                                            <div className="singleDices">
                                                <img src={Dot6} alt="Dot6" />
                                            </div>
                                            <div className="singleDices">
                                                <img src={Dot1} alt="Dot1" />
                                            </div>
                                        </div>
                                    </td>
                                    <td><span className="color-green">Big</span></td>
                                </tr>
                                <tr>
                                    <td># 23265323</td>
                                    <td>
                                        <div className="threeDices">
                                            <div className="singleDices">
                                                <img src={dice1} alt="Dot4" />
                                            </div>
                                            <div className="singleDices">
                                                <img src={dice6} alt="Dot6" />
                                            </div>
                                            <div className="singleDices">
                                                <img src={dice3} alt="Dot1" />
                                            </div>
                                        </div>
                                    </td>
                                    <td><span className="color-green">FIX THIS LINE</span></td>
                                </tr>
                                {history.map( game => {
                                return <tr key={v4()}>
                                    <td># {game.game_id}</td>
                                    
                                    <td>
                                        <div className="threeDices">
                                            <div className="singleDices">
                                                {dice(game.result1)}
                                            </div>
                                            <div className="singleDices">
                                                {dice(game.result2)}
                                            </div>
                                            <div className="singleDices">
                                                {dice(game.result3)}
                                            </div>
                                        </div>
                                    </td>

                                    {side(game.won_side)}
                                    
                                    {/*<td><span className="color-green">{game.won_side}</span></td>*/}

                                </tr>
                                })}
                            </tbody>
                        </table>
                    </div>
                </Modal.Body>
            </Modal>
        </>
     );
}

export default ResultHistory;

