import React from 'react';
import { useState, useEffect } from 'react';

import {socket} from '../../services/coinflip';
import {userAccount} from '../../web3/testnet'

import Coin1 from '../../assets/img/coin/coin-1.svg';
import Coin2 from '../../assets/img/coin/coin-2.svg';

function CoinFlipBox() {
    const [history, setHistory] = useState([]);
    useEffect(() => {
        socket.emit("join", {"player": userAccount});

        socket.on('RESULT', (...args) => {
            setHistory(args[0].results);
        });
        
        return () => {
          socket.off('RESULT');
        };
    }, []);

    function result (value) {
        if (value === "tail") return <img src={Coin1} alt="Coin Flip Xanh Chin, Xanh Chin, xanhchin.io" className='CoinItem inactiveCoin' />
        else if (value === "head") return <img  src={Coin2} alt="Coin Flip Xanh Chin, Xanh Chin, xanhchin.io" className='CoinItem' />
        else return <></>
    }
    return ( 
        <div className="coinFlip_area">
            <div className="flipBox">
                <div className="coinFlipCircle">
                    {result(history.slice(-1)[0])}
                    {/*<img src={Coin2} alt="" />*/}
                    {/* <img src={} alt="" /> */}
                </div>
                <div className="coinHeadTails">
                    <div className="coinHeTe">
                        <img src={Coin2} alt="Coin Flip Xanh Chin, Xanh Chin, xanhchin.io" /> Heads
                    </div>
                    <div className="coinHeTe">
                        <img src={Coin1} alt="Coin Flip Xanh Chin, Xanh Chin, xanhchin.io" /> Tails
                    </div>
                </div>
            </div>
        </div>
     );
}

export default CoinFlipBox;