import React from 'react';
import { useState, useEffect, useRef } from 'react';
import { v4 } from 'uuid';

import {socket} from '../../services/taixiu';

import BetStart from './BetStart';
import BetStart2 from './BetStart2';

import GameRoundStart from './GameRoundStart';
import BigBet from './BigBet';
import SmallBet from './SmallBet';
import BeatRank from '../../components/BeatRank';

import Colorcircle from '../../assets/img/dices/color-circle.svg';

function BigSmallBet() {
    // gState have 3 values: COUNTDOWN, WAITING, RESULT
    const [round, setRound] = useState("WAITING");
    const [data, setData] = useState("");
    const [rank, setRank] = useState(0);
    const [result, setResult] = useState(0);
    const [history, setHistory] = useState([]);
    // <span className="colorCircleItem yellowCircle"></span>

    function circle (value) {
        if (value === "xiu") return <span title="XIU" key={v4()} className="colorCircleItem redCircle"></span>
            else return <span title="TAI" key={v4()} className="colorCircleItem yellowCircle"></span>
    }
    useEffect(() => {
        socket.on('COUNTDOWN', (...args) => {
            //console.log(args[0]);
            setRank(args[0].game_id);
            setData(args[0].timeleft);
            //console.log(args[0]);
            setRound('COUNTDOWN');
            //console.log(history);
          
            if ( args[0].timeleft === 0) setRound('WAITING');
            //console.log(round);
        });
        //console.log("RESULT1");
        socket.on('RESULT', (...args) => {
            //console.log("RESULT2.1");
            setResult(args[0]); // {game_id: '2609', won_side: 'xiu', result1: 2, result2: 6, result3: 2}
            setRound('RESULT');
            let temp = history;
            //console.log("temp", temp);

            temp.push(args[0].won_side);
            temp = temp.slice(-10);
            setHistory(temp);
            //console.log(history);
            //console.log("RESULT2.2");
            //console.log(args[0]);
            //console.log(round);
        });
        //console.log("RESULT3");
    
        return () => {
          socket.off('COUNTDOWN');
          socket.off('RESULT');
        };
    }, []);

    if (round == 'COUNTDOWN') 
    return ( 
        <div className="bigsmallBet">
            <BeatRank rank={rank}/>
            <div className="betResult">
                <div className="color_circle">
                    {history.map( game => {
                                return circle(game);
                            }
                    )}
                </div>
                <BetStart data={data}/>
            </div>
            <div className="betrow row">
                <div className="col-6">
                    <BigBet/>
                </div>
                <div className="col-6">
                    <SmallBet/>
                </div>
            </div>
        </div>
    ); else if (round == 'WAITING') {
        return ( 
            <div className="bigsmallBet">
                <BeatRank rank={rank}/>
                <div className="betResult">
                <div className="color_circle">
                    {history.map( game => {
                                return circle(game);
                            }
                    )}
                </div>
                    <BetStart2/>
                </div>
                <div className="betrow row">
                    <div className="col-6">
                        <BigBet/>
                    </div>
                    <div className="col-6">
                        <SmallBet/>
                    </div>
                </div>
            </div>
         );
    } else 

    return ( 
        <div className="bigsmallBet">
            <BeatRank rank={rank}/>
            <div className="betResult">
                <div className="color_circle">
                    {history.map( game => {
                                return circle(game);
                            }
                    )}
                </div>
                <GameRoundStart result={result}/>
            </div>
            <div className="betrow row">
                <div className="col-6">
                    <BigBet/>
                </div>
                <div className="col-6">
                    <SmallBet/>
                </div>
            </div>
        </div>
     );
}

export default BigSmallBet;