import React from 'react';
import { useState, useEffect } from 'react';

import {socket} from '../../services/baucua';

import Game1 from '../../assets/img/baucua/bau.png';
import Game2 from '../../assets/img/baucua/cua.png';
import Game3 from '../../assets/img/baucua/ca.png';
import Game4 from '../../assets/img/baucua/ga.png';
import Game5 from '../../assets/img/baucua/tom.png';
import Game6 from '../../assets/img/baucua/nai.png';

function GameCardList({chooseSide}) {
    const cNameOriginal = {
        bau: "gameCardItem",
        ca: "gameCardItem",
        cua: "gameCardItem",
        ga: "gameCardItem",
        nai: "gameCardItem",
        tom: "gameCardItem"
    };
    const [total, setTotal] = useState({
        bau: "0.0000 DIME",
        ca: "0.0000 DIME",
        cua: "0.0000 DIME",
        ga: "0.0000 DIME",
        nai: "0.0000 DIME",
        tom: "0.0000 DIME"
    });
    const [cName, setCName] = useState(cNameOriginal);

    useEffect(() => {
        socket.on('TOTAL_BET', (...args) => {
            setTotal(args[0]);
        });
    
        return () => {
          socket.off('TOTAL_BET');
        };
    }, []);

    function side(value) {
        let temp = cNameOriginal;
        temp[value] = "gameCardItem active";
        setCName(temp);
        chooseSide(value);
    }

    return ( 
        <div className="gameCardList row">
            <div className="col-4">
                <div className={cName.bau} onClick={() => side("bau")}>
                    <h2>Gourd</h2>
                    <div className="gameCardItemImg">
                        <img src={Game1} alt="" />
                        <div className="gameCardNumber">{parseFloat(total.bau).toFixed(2)}</div>
                    </div>
                </div>
            </div>
            <div className="col-4">
                <div className={cName.cua} onClick={() => side("cua")}>
                    <h2>Crab</h2>
                    <div className="gameCardItemImg">
                        <img src={Game2} alt="" />
                        <div className="gameCardNumber">{parseFloat(total.cua).toFixed(2)}</div>
                    </div>
                </div>
            </div>
            <div className="col-4">
                <div className={cName.ca} onClick={() => side("ca")}>
                    <h2>Fish</h2>
                    <div className="gameCardItemImg">
                        <img src={Game3} alt="" />
                        <div className="gameCardNumber">{parseFloat(total.ca).toFixed(2)}</div>
                    </div>
                </div>
            </div>
            <div className="col-4">
                <div className={cName.ga} onClick={() => side("ga")}>
                    <h2>Roaster</h2>
                    <div className="gameCardItemImg">
                        <img src={Game4} alt="" />
                        <div className="gameCardNumber">{parseFloat(total.ga).toFixed(2)}</div>
                    </div>
                </div>
            </div>
            <div className="col-4">
                <div className={cName.tom} onClick={() => side("tom")}>
                    <h2>shrimp</h2>
                    <div className="gameCardItemImg">
                        <img src={Game5} alt="" />
                        <div className="gameCardNumber">{parseFloat(total.tom).toFixed(2)}</div>
                    </div>
                </div>
            </div>
            <div className="col-4">
                <div className={cName.nai} onClick={() => side("nai")}>
                    <h2>Deer</h2>
                    <div className="gameCardItemImg">
                        <img src={Game6} alt="" />
                        <div className="gameCardNumber">{parseFloat(total.nai).toFixed(2)}</div>
                    </div>
                </div>
            </div>
        </div>
     );
}

export default GameCardList;

