import React from 'react';
import { useState, useEffect } from 'react';

import {socket} from '../../services/baucua';

import Card1 from '../../assets/img/baucua/bau.svg';
import Card2 from '../../assets/img/baucua/tom.svg';
import Card3 from '../../assets/img/baucua/nai.svg';

import dice0 from '../../assets/img/baucua/0.svg';
import dice1 from '../../assets/img/baucua/1.svg';
import dice2 from '../../assets/img/baucua/2.svg';
import dice3 from '../../assets/img/baucua/3.svg';
import dice4 from '../../assets/img/baucua/4.svg';
import dice5 from '../../assets/img/baucua/5.svg';
import dice6 from '../../assets/img/baucua/6.svg';

function dice (v) {
    if (v === 1) return <img src={dice1} />
    if (v === 2) return <img src={dice2} />
    if (v === 3) return <img src={dice3} />
    if (v === 4) return <img src={dice4} />
    if (v === 5) return <img src={dice5} />
    if (v === 6) return <img src={dice6} />
}

function GourdCrabStartWill() {
    const [time, setTime] = useState(0);
    const [result, setResult] = useState({
        result1: 0, result2: 0, result3: 0
    });

    useEffect(() => {
        socket.on('COUNTDOWN', (...args) => {
            //console.log(args[0]);
            setTime(args[0].timeleft);
        });
        socket.on('RESULT', (...args) => {
            //console.log(args[0]); // {game_id: '5603', result1: 1, result2: 5, result3: 4}
            setResult(args[0]);
        });

        return () => {
          socket.off('COUNTDOWN');
          socket.off('RESULT');
        };
    }, []);

    return ( 
        <div className="gameCardStart">
            <div className="gameCounter">
                <h2 className=''>{time}</h2>
                <p>Game will be start after </p>
            </div>
            <div className="gameStartCardBox">
                <div className="gameWillCard">
                    {dice(result.result1)}
                </div>
                <div className="gameWillCard">
                    {dice(result.result2)}
                </div>
                <div className="gameWillCard">
                    {dice(result.result3)}
                </div>
            </div>
        </div>
     );
}

export default GourdCrabStartWill;

