import React from 'react';

import Dot1Red from '../../assets/img/dices/1.png';
import Dot4Red from '../../assets/img/dices/4.png';
import Dot6Red from '../../assets/img/dices/dot-6-red.svg';

import dice1 from '../../assets/img/dices/1.png';
import dice2 from '../../assets/img/dices/2.png';
import dice3 from '../../assets/img/dices/3.png';
import dice4 from '../../assets/img/dices/4.png';
import dice5 from '../../assets/img/dices/5.png';
import dice6 from '../../assets/img/dices/6.png';

function GameRoundStart(props) {
    //console.log(props.result);
    function dice (v) {
        if (v === 1) return <img src={dice1} />
        if (v === 2) return <img src={dice2} />
        if (v === 3) return <img src={dice3} />
        if (v === 4) return <img src={dice4} />
        if (v === 5) return <img src={dice5} />
        if (v === 6) return <img src={dice6} />
    }
    return ( 
        <div className="gameRound">
            <div className="gameRoundTop">
                <span className="game_Round">2</span>
                <p>Game round will be start soon </p>
            </div>
            <div className="gameroundResult">
                {dice(props.result.result1)}{dice(props.result.result2)}{dice(props.result.result3)}
            </div>
        </div>
     );
}

export default GameRoundStart;