import React from 'react';
import { useState, useEffect } from 'react';

import { v4 } from 'uuid';

import {socket} from '../../services/hilo';
import {userAccount} from '../../web3/testnet'

import HiImage from '../../assets/img/hilo/hi.svg';
import LoImage from '../../assets/img/hilo/lo.svg';
import card from '../../assets/img/poker_cards/2_heart.svg';

import Card1 from '../../assets/img/poker_cards/2_heart.svg';
import BigCard from '../../assets/img/card-2.png';

function CardSelectBox() {
    const [history, setHistory] = useState([]);
    useEffect(() => {
        //socket.emit("join", {"player": "arvernorix12"});
        socket.emit("join", {"player": userAccount});

        socket.on('RESULT', (...args) => {
            setHistory(args[0].results);
        });
        
        return () => {
          socket.off('RESULT');
        };
    }, []);

    function show (value) {
        return <div key={v4()}>{value.key}</div>
    }

    return ( 
        <div className="card_select_area">
            <div className="cardList_area">
                {history.slice(-10).map(value => show(value))}
                <img src={Card1} className='SingleCard' alt="SingleCard" />
                <img src={Card1} className='SingleCard' alt="SingleCard" />
                <img src={Card1} className='SingleCard' alt="SingleCard" />
                <img src={Card1} className='SingleCard' alt="SingleCard" />
                <img src={Card1} className='SingleCard' alt="SingleCard" />
                <img src={Card1} className='SingleCard' alt="SingleCard" />
                <img src={Card1} className='SingleCard' alt="SingleCard" />
                <img src={Card1} className='SingleCard' alt="SingleCard" />
                <img src={Card1} className='SingleCard' alt="SingleCard" />
                <img src={Card1} className='SingleCard' alt="SingleCard" />
            </div>
            <div className="threeCardBox">
                <div className="row">
                    <div className="col-4">
                        <div className="cardDragBox">
                            <div className="cardDragImg">
                                <div className="cardImgText hiLowImg">
                                    {/* <p>100.565656</p>
                                    <h3>DIMS</h3> */}
                                    <img src={HiImage} alt="HiImage" />
                                </div>
                            </div>
                            <h3>Higher or Same</h3>
                        </div>
                    </div>
                    <div className="col-4">
                        <div className="cardDragBox ">
                            <div className="cardDragImg">
                                <div className="cardImgText hiLowImg">
                                    {/*<p>100.565656</p>
                                    <h3>DIMS</h3>*/}
                                    <img src={card} alt="HiImage" />
                                </div>
                            </div>
                            <h3>King is Highest <br /> Ace is Lowest</h3>
                            
                        </div>
                    </div>
                    <div className="col-4">
                        <div className="cardDragBox">
                            <div className="cardDragImg">
                                <div className="cardImgText">
                                    {/* <p>100.565656</p>
                                    <h3>DIMS</h3> */}
                                    <img src={LoImage} alt="HiImage" />
                                </div>
                            </div>
                            <h3>Higher or Same</h3>
                        </div>
                    </div>
                </div>
            </div>
        </div>
     );
}
// {history.slice(-1)[0].key}
export default CardSelectBox;