import React from 'react';
import { useState, useEffect } from 'react';

import {socket} from '../../services/xocdia';

import BeatRank from '../../components/BeatRank';
import ShakeDishCard from './ShakeDishCard';


function ShakeBetingGround({rank, chooseSide}) {

    useEffect(() => {
        socket.on('COUNTDOWN', (...args) => {
            console.log(args[0]);
        });

        return () => {
          socket.off('COUNTDOWN');
        };
    }, []);

    return ( 
        <div className="bigsmallBet">
            <BeatRank rank={rank}/>
            <ShakeDishCard chooseSide={chooseSide}/>
        </div>
     );
}

export default ShakeBetingGround;

